.dropzone{min-height:100px;border:2px dashed #babfc7;background:#f8f8f8;display:flex;justify-content:center;align-items:center}.dropzone:focus{outline:0}.dropzone p{margin:0 0 .5rem 0}.dz-thumb{display:inline-block;border-radius:2px;border:1px solid #eaeaea;margin-bottom:8px;margin-right:8px;padding:4px;box-sizing:border-box;text-align:center}.thumb-container{display:flex;flex-direction:row;flex-wrap:wrap;margin-top:16px}.dz-thumb-inner{display:flex;min-width:0;overflow:hidden;width:15rem;height:12rem}.dz-img{display:block;width:100%;height:100%;object-fit:cover}
